.about {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* padding: 1rem; */
  /* padding-top: 6rem; */
  color: #fff;
  min-height: 100vh;
  background-image: url("../images/background2.png");
}

.about__video {
  padding: 2rem;
  min-width: 50%;
}

.about__img {
  width: 100%;
  height: 100%;
}

.about__iframe {
  width: 100%;
  min-height: 15rem;
  height: 100%;
}

.about h1 {
  letter-spacing: 0.05rem;
  text-align: start;
  font-size: calc((4.1 - 1) * 1.2vw + 1rem);
  line-height: calc(1.3 * (1 + (1 - 5.1)/25));
}

.about__text {
  min-width: 50%;
  padding: 2rem;
}

.about__text p {
  font-size: calc((1.1 - 1) * calc(.012 * min(100vh, 900px)) + 1rem);
  word-wrap: break-word;
  font-family: 'Helvetica', sans-serif;
}